import { combineReducers } from 'redux'
// import {responsiveDrawer} from '../../../src';

const demoReduces = (state = {}, action) => {
  switch (action.type) {
    default:
      return state
  }
}

const reducers = combineReducers({
  demoReduces
})

export default reducers



